import blaze from "external:@ruteni/blaze/v1";
import sanitizer from "external:@ruteni/security/v1";
import { registerElementBundleResolver } from "external:@ruteni/translation/v1";
import QuotquotElement from "external:@ruteni/quotquot/v1:element";
import EventsPlugin from "external:@ruteni/quotquot/v1:plugin.events";
import FluentPlugin from "external:@ruteni/quotquot/v1:plugin.fluent";
import StylePlugin from "external:@ruteni/quotquot/v1:plugin.style";
import ClassPlugin from "external:@ruteni/quotquot/v1:plugin.class";

import html from "./index.html";
import index from "./index.json";
import resources from "./resources.js";

registerElementBundleResolver(index, import.meta.url);

class StoreHomepageHTMLElement extends QuotquotElement {
    constructor() {
        super({
            template: sanitizer.createTemplate(index.tagName, html),
            state: {
                styles: blaze.getUrls(index, resources),
                events: {
                }
            },
            Plugins: [EventsPlugin, FluentPlugin, StylePlugin, ClassPlugin]
        });
    }
}

window.customElements.define(index.tagName, StoreHomepageHTMLElement);
